<template>
    <div class="views news">
        <navigate title="资讯中心" position="fixed" :color="color" :backgroundColor="bgc" :shadow="shadow"/>
        <div class="scroll-wrapper" @scroll="onScroll" ref="scroll">
            <div class="header" ref="header">
                <div class="list">
                    <div class="list-item" v-for="(item,index) in header" :key="index"
                         :class="{'list-item-active': $route.query.type_id === item.id}"
                         @click="goTypeList(item)">
                        <img class="list-item-icon"
                             :src="$route.query.type_id === item.id ? item.activeIcon : item.icon"/>
                        <span class="list-item-label">{{item.typeName}}</span>
                    </div>
                </div>
            </div>
            <div class="news-content">
                <div class="assortment border-1px-b" v-if="$route.query.type_name === '航道通告'"
                     @click="active = true">
                    <span style="margin-right: 0.5em">{{assortment.name}}</span>
                    <mu-icon :class="{active: active}" value="expand_more"/>
                </div>
                <mu-load-more style="min-height: 50vh" :refreshing="refreshing" :loading="loading" ref="content"
                              @refresh="refresh"
                              @load="load">
                    <div class="list" v-if="list.length">
                        <mu-ripple class="list-item border-1px-b" v-for="(item, index) in list" :key="index"
                                   @click="handlerNewItem(item, index, $route.query.type_name)">
                            <div class="pic" :style="{backgroundImage: `url(${item.pic})`}" v-if="item.pic"></div>
                            <div class="new">
                                <div class="title">{{item.title}}</div>
                                <div class="info">
                                    <div>{{item.publisher}}</div>
                                    <div>{{item.createTime | formatPast}}</div>
                                </div>
                            </div>
                        </mu-ripple>
                    </div>
                    <no-success :text="`暂时没有查询到《${$route.query.type_name || '新闻资讯'}》列表`" height="60vh" v-else/>
                </mu-load-more>
            </div>
        </div>

        <actionsheet v-model="active" :actions="actions" cancel-text="取消" @select="onSelect" @cancel="onCancel"/>

        <transition name="fade-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import NoSuccess from "../../components/no-success/no-success"
    import Actionsheet from 'vant/lib/actionsheet'
    import 'vant/lib/actionsheet/style'
    import formatTime from "../../common/utils/time"
    import {getNewHeader, getNewList, getByType, getbyarea} from "../../api/news"
    import {queryTypeDictByFid} from "../../api/search"
    import {isArray} from "../../types/index"

    export default {
        name: "news",
        data() {
            return {
                /***
                 * 区域选择器
                 */
                active: false,
                actions: [],
                assortment: '全部',
                /***
                 * 导航栏样式
                 */
                color: "#fff",
                shadow: false,
                bgc: "transparent",
                scrollTop: 0,
                /***
                 * 下拉刷新&上拉加载
                 */
                refreshing: false,
                loading: false,
                /***
                 * 搜索条件
                 */
                params: {
                    pageNumber: 0,
                    pageSize: 15,
                },
                /***
                 * 头部导航列表
                 */
                header: [],
                /***
                 * 搜索结果列表
                 */
                list: []
            }
        },
        async created() {
            this.header = [{
                "id": "001",
                "typeKey": "scale",
                "typeName": "航道尺度",
                "typeState": "1",
                "icon": "static/icon/news_scale.png",
                "activeIcon": "static/icon/news_scale_current.png"
            }, {
                "id": "002",
                "typeKey": "weather",
                "typeName": "航道天气",
                "typeState": "1",
                "icon": "static/icon/news_weather.png",
                "activeIcon": "static/icon/news_water_level_current.png"
            }, {
                "id": "003",
                "typeKey": "notice",
                "typeName": "航道通告",
                "typeState": "1",
                "icon": "static/icon/news_notice.png",
                "activeIcon": "static/icon/news_notice_current.png"
            }, {
                "id": "004",
                "typeKey": "news",
                "typeName": "航道新闻",
                "typeState": "1",
                "icon": "static/icon/news_news.png",
                "activeIcon": "static/icon/news_news_current.png"
            }, {
                "id": "005",
                "typeKey": "law",
                "typeName": "法律法规",
                "typeState": "1",
                "icon": "static/icon/news_law.png",
                "activeIcon": "static/icon/news_law_current.png"
            }]

            this.actions = await queryTypeDictByFid('029')

            this.assortment = this.actions[0]
        },
        activated() {
            this.refresh()
        },
        methods: {
            /***
             * 点击头部导航按钮
             * @param type  => 类型
             */
            goTypeList(type) {
                if (this.$route.query.type_id === type.id) {
                    this.$router.replace(`/news`)
                } else {
                    this.$router.replace(`/news?type_id=${type.id}&type_name=${type.typeName}`)
                }

                this.assortment = this.actions[0]

                this.refresh()
            },
            // 点击单条新闻
            handlerNewItem(item, index, typeName) {
                this.$router.push({
                    name: `news-detail`,
                    params: {
                        id: item.id,
                        typeName: typeName
                    }
                })
            },
            /***
             * 顶部导航样式调整
             */
            onScroll() {
                const top = this.$refs.scroll.scrollTop
                const max = this.$refs.header.offsetHeight - 40

                if (top > max) {
                    this.color = "#fff"
                    this.bgc = ''
                    this.shadow = true
                } else {
                    this.color = "#fff"
                    this.bgc = "transparent"
                    this.shadow = false
                }
            },
            // 刷新
            refresh() {
                this.refreshing = true
                this.$refs.content.scrollTop = 0

                this.getNewList()
            },
            //  加载
            load() {
                this.loading = true
                this.params.pageNumber++


                if (this.$route.query.type_id) {
                    this.params.typeId = this.$route.query.type_id

                    if (this.$route.query.type_name === '航道通告') {
                        const params = {
                            areaId: this.assortment.id,
                            pageNumber: this.params.pageNumber,
                            pageSize: this.params.pageSize
                        }

                        getbyarea(params).then(res => {
                            this.list.push(...isArray(res))
                            this.loading = false
                        })
                    } else {
                        getByType(this.params).then(res => {
                            this.list.push(...isArray(res))
                            this.loading = false
                        })
                    }
                } else {
                    this.params.typeId = undefined

                    getNewList(this.params).then(res => {
                        this.list.push(...isArray(res))
                        this.loading = false
                    })
                }
            },
            /***
             * 获取导航列表
             */
            getNewList() {
                if (this.$route.query.type_id) {
                    this.params.pageNumber = 0
                    this.params.typeId = this.$route.query.type_id

                    if (this.$route.query.type_name === '航道通告') {
                        const params = {
                            areaId: this.assortment.id,
                            pageNumber: this.params.pageNumber,
                            pageSize: this.params.pageSize
                        }

                        getbyarea(params).then(res => {
                            this.list = isArray(res)
                            this.refreshing = false
                            this.$toast.clear()
                        })
                    } else {
                        getByType(this.params).then(res => {
                            this.list = isArray(res)
                            this.refreshing = false
                            this.$toast.clear()
                        })
                    }
                } else {
                    this.params.typeId = undefined
                    this.params.pageNumber = 0

                    getNewList(this.params).then(res => {
                        this.list = isArray(res)
                        this.refreshing = false
                        this.$toast.clear()
                    })
                }
            },
            onSelect(item) {
                this.active = false
                this.assortment = item

                this.getNewList()
            },
            onCancel() {
                this.active = false
            }
        },
        filters: {
            formatPast: formatTime.formatPast
        },
        components: {
            NoSuccess,
            Navigate,
            Actionsheet
        }
    };
</script>

<style lang="stylus">
    @import '../../common/stylus/variable.styl';

    .news
        width 100%
        height 100%
        background-color #fff
        overflow hidden
        > .scroll-wrapper
            width 100%
            height 100%
            .header
                position relative
                padding-top 46.33333%
                height 0
                background transparent url('../../../static/image/news_top_bg.png') no-repeat center center
                background-size cover
                .list
                    position absolute
                    bottom 0
                    left 0
                    right 0
                    display flex
                    .list-item
                        flex 1
                        display flex
                        flex-direction column
                        align-items center
                        padding 15px 10px
                        &.list-item-active .list-item-icon
                            background-color: #fff
                        .list-item-icon
                            width 44px
                            height 44px
                            padding 10px
                            color #fff
                            border-radius 50%
                            background-color rgba(0, 0, 0, .3)
                            box-shadow 0 0 7px -1px #fff
                            transition all .25s
                        .list-item-label
                            margin-top 10px
                            color #fff
                            font-size $font-size-ss

            .news-content
                min-height 50%
                .assortment
                    display flex
                    align-items center
                    justify-content center
                    height 50px
                    line-height 50px
                    .mu-icon
                        transition all 1s
                    .mu-icon.active
                        transform rotate(180deg)
                .list .list-item
                    position relative
                    display flex
                    padding 15px
                    .pic
                        margin-right 10px
                        width 120px
                        height 85px
                        background-position center center
                        background-repeat no-repeat
                        background-size cover

                    .new
                        flex 1
                        display flex
                        flex-direction column
                        justify-content space-between

                        .title
                            margin-bottom 10px
                            line-height 1.25
                            font-size $font-size-ml
                            letter-spacing 0.8px

                        .info
                            display flex
                            justify-content space-between
                            font-size $font-size-sm
                            color #ACACAC


</style>



